import React from "react";
import { Switch, Redirect } from "react-router-dom";
import {
  PrivateRouteWithLayout,
  // RouteWithLayout,
  PublicRouteWithLayout,
  // PrivateRouteWithLayout,
} from "../components";
import { MainWithSidebar, MinimalLayout } from "../layouts";

import { Dashboard, Login, ChangePassword, Setting, Customer } from "../pages";
function Routes() {
  return (
    <>
      <Switch>
        <Redirect exact from="/" to="/login" />
        <PublicRouteWithLayout
          component={Login}
          exact
          layout={MinimalLayout}
          path="/login"
        />
        {/* --------------------------------------- Dashboard --------------------------------------- */}
        <PrivateRouteWithLayout
          component={Dashboard}
          exact
          layout={MainWithSidebar}
          path="/dashboard"
        />
        {/* --------------------------------------- Change Password --------------------------------------- */}
        <PrivateRouteWithLayout
          component={ChangePassword}
          exact
          layout={MainWithSidebar}
          path="/change-password"
        />

        {/* --------------------------------------- Setting --------------------------------------- */}

        <PrivateRouteWithLayout
          component={Setting}
          exact
          layout={MainWithSidebar}
          path="/Setting"
        />
        {/* --------------------------------------- Customer--------------------------------------- */}
        <PrivateRouteWithLayout
          component={Customer}
          exact
          layout={MainWithSidebar}
          path="/Customer"
        />
      </Switch>
    </>
  );
}

export default Routes;
